.footer-col>.social-links {
    display: flex;
    /* border-left: 2px solid green; */
    margin-left: -5px;
}

.footer-col>.social-links>.footer-svgs {
    width: 40px;
    height: 40px;
    /* background-color: #504949; */
    background-color: white;
    margin: 0px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-top: 8px;
    padding-left: 0px;
    border-radius: 50%;
    padding-bottom: 5px;
    color: white;
}

.footer-col>.social-links>.footer-svgs:hover {
    color: #24262b;
    background-color: rgb(193, 126, 255);
}

@media (max-width:574px) {
    .row {
        flex-direction: column;
        width: 100vw;
    }
    .footer-col {
        width: 100vw;
    }
    .footer-col ul {
        display: flex;
        justify-content: space-evenly;
    }
    .footer-col h4 {
        margin-left: 20px;
    }
    .footer-col ul a li p {
        margin-right: 10px;
    }
    .footer-svgs>a {
        margin-left: -16px;
        /* margin-right: 10px; */
    }
    .footerMain {
        width: 100vw;
        /* margin-left: 120px; */
    }
    .footer-col>.social-links>.footer-svgs {
        width: 40px;
        height: 40px;
        /* background-color: #504949; */
        background-color: white;
        margin: 0px 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding-top: 8px;
        padding-left: 15px;
        border-radius: 50%;
        padding-bottom: 5px;
        color: white;
    }
    .footer-col>.social-links {
        margin-left: 55px;
    }
}